.navbar{
    width: 260px;
    min-width: 260px;
    height: 100vh;
    padding: 20px 12px 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-right: 1px solid rgb(219, 219, 219);
    position: relative;
    top: 0;
    margin-bottom: auto;
}
.navbar-top{
    
}
.navbar-logo{
    width: fit-content;
    margin: 25px 12px 25px 0;
    color: #000000;
}
.navbar-logo svg{
    width: 103px;
    height: 29px;
}

.navbar-links-holder{
    display: flex;
    flex-direction: column;
    margin-top: 30px;
}

.navbar-link{
    margin: 2px 0;
    padding: 8px 12px;
    display: flex;
    align-items: center;
    border-radius: 8px;
    transition: .2s;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
}
.navbar-link-selected{
    font-weight: 700;
    background: #f1f1f165;
}
.navbar-link:hover{
    background: rgba(0, 0, 0, .05);
}
.navbar-link svg{
    margin-right: 16px;
    margin-top: 4px;
}
.navbar-link-pp{
    width: 28px;
    height: 28px;
    border-radius: 50%;
    margin-right: 16px;
    margin-top: 4px;
    border: 1px solid rgb(209, 208, 208);
    padding: 2px;
}











.navbar-mobile{
    display: none;
}
.navbar-mobile-header {
  position: fixed;
  top: 20px;
  left: 24px;
  z-index: 6;
}
.navbar-mobile-header svg{
    width: 103px;
    height: 29px;
}
.navbar-mobile-header > .navbar-mobile-header span {
  text-transform: none;
  opacity: 0.5;
  font-weight: 300;
  font-size: 12px;
}
.navbar-mobile-input{
    display: none;
}
.navbar-mobile-input + .navbar-mobile-label {
  position: fixed;
  top: 20px;
  right: 24px;
  height: 20px;
  width: 15px;
  z-index: 5;
}
.navbar-mobile-input + .navbar-mobile-label span {
  position: absolute;
  width: 100%;
  height: 2px;
  top: 50%;
  margin-top: -1px;
  left: 0;
  display: block;
  background: #020304;
  transition: 0.5s;
}
.navbar-mobile-input + .navbar-mobile-label span:first-child {
  top: 3px;
}
.navbar-mobile-input + .navbar-mobile-label span:last-child {
  top: 16px;
}
.navbar-mobile-label:hover {
  cursor: pointer;
}
.navbar-mobile-input:checked + .navbar-mobile-label span {
  opacity: 0;
  top: 50%;
}
.navbar-mobile-input:checked + .navbar-mobile-label span:first-child {
  opacity: 1;
  transform: rotate(405deg);
}
.navbar-mobile-input:checked + .navbar-mobile-label span:last-child {
  opacity: 1;
  transform: rotate(-405deg);
}
.navbar-mobile-input ~ .navbar-mobile-navigation {
  background: white;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  z-index: 3;
  transition: 0.5s;
  transition-delay: 0.5s;
  overflow: hidden;
}
.navbar-mobile-input ~ .navbar-mobile-navigation > ul {
  text-align: center;
  position: absolute;
  top: 35%;
  left: 20%;
  right: 20%;
  list-style: none;
}
.navbar-mobile-input ~ .navbar-mobile-navigation > ul > li {
  opacity: 0;
  transition: 0.5s;
  transition-delay: 0s;
}
.navbar-mobile-input ~ .navbar-mobile-navigation > ul > li > a {
  text-decoration: none;
  text-transform: uppercase;
  color: #020304;
  font-weight: 700;
  display: block;
  margin: 0 0 60px 0;
}
.navbar-mobile-input ~ .navbar-mobile-navigation > ul > li:last-child > a{
    margin-bottom: 0;
}
.navbar-mobile-input:checked ~ .navbar-mobile-navigation {
  height: 100%;
  transition-delay: 0s;
}
.navbar-mobile-input:checked ~ .navbar-mobile-navigation > ul > li {
  opacity: 1;
  transition-delay: 0.5s;
}




@media screen and (max-width: 1000px) {
    .navbar{
        display: none;
    }
    .navbar-mobile{
        display: flex;
    }
}