.signup {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: #ffffff;
    grid-gap: 10px;
}
  
  .login-wrapper {
    display: flex;
    align-items: center;
    grid-gap: 32px;
  }
  
  .account-section {
    width: 350px;
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
  }
  
  .login-box {
    width: 100%;
    padding: 65px 40px 20px 40px;
    border: 1px solid rgb(219, 219, 219);
    border-radius: 1px;
    background-color: #ffffff;
    text-align: center;
  }
  
  .login-logo {
    background-image: url(../../Images/Icons/instagram_original.png);
    background-position: 0px -52px;
    background-size: auto;
    width: 175px;
    height: 51px;
    background-repeat: no-repeat;
    display: inline-block;
    margin-bottom: 36px;
    cursor: pointer;
  }
  
  .login-fields {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 6px;
    margin-bottom: 14px;
  }

  .login-field{
    position: relative;
  }
  
  .login-field-input-placeholder{
    position: absolute;
    font-size: 12px;
    top: 11px;
    left: 8px;
    color: #6d6d6d;
    transition: .2s;
    pointer-events: none;
  }

  .input-field {
    width: 100%;
    height: 36px;
    padding: 8px 8px 8px 8px;
    border-radius: 1px;
    outline: none;
    background-color: #fafafa;
    border: 1px solid rgb(219, 219, 219);
    font-size: 12px;
  }

  .login-field-active .login-field-input-placeholder{
    display: block;
    top: 6px;
    font-size: 8px;
    opacity: 1;
    transition: 0.01s;
  }


  .login-field-active .input-field{
    padding: 16px 8px 8px 8px;
  }
  
  .login-button {
    width: 100%;
    padding: 10px;
    background-color: #4cb5f9;
    color: white;
    border: none;
    border-radius: 8px;
    font-weight: 600;
    cursor: pointer;
    margin-bottom: 10px;
    transition: .2s;
  }
  
  .login-button:hover {
    background-color: #0095f6;
  }
  
  .error-message {
    color: red;
    font-size: 14px;
    margin-top: 12px;
    margin-bottom: 6px;
    font-weight: 400;
  }
  
  .forgot-password-link a {
    text-decoration: none;
    color: #00376B;
    font-size: 12px;
  }
  
  .new-account {
    width: 100%;
    max-width: 350px;
    padding: 25px 40px;
    border: 1px solid rgb(219, 219, 219);
    border-radius: 1px;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #000000;
    font-size: 14px;
    font-weight: 400;
  }
  
  .new-account span{
    font-size: inherit;
    font-weight: inherit;
  }

  .new-account span a {
    color: #0095f6;
    cursor: pointer;
    margin-left: 3px;
    font-size: inherit;
    font-weight: inherit;
  }
  